html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --off-white: #f1e8e8;
}
 .App {
   background-color: rgb(190, 248, 242);
   height: 100%;
 }

.section{
  border-radius: 20px;
  background:white;
}
.section-dark {
  border-radius: 20px;
  background: #333;
  color: white;
}

.section-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
}

.section-content h1 {
  font-family: sans-serif;
}

.altbody {
  padding-top: 10px;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 10px;
}

.toolbar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #f1e8e8;
    height: 75px;
}

.toolbar_logo{
    height: 60px;
    width: 160px;
    margin-left: 2.6rem;
}
.toolbar_navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}
.spacer {
    flex: 1 1;
}

.toolbar_navigation-items ul {
    list-style: none;
    font-size: 18px;
    font-family: cursive;
    margin: 0;
    padding: 0;
    display: flex;
    margin-right: 2.6rem;
}

.toolbar_navigation-items li{
    padding: 0 0.5rem;
}

.toolbar_navigation-items a{
    color:rgb(207, 86, 86);
    text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active{
    color: black;
    cursor: pointer;
}

@media (max-width: 768px){
    .toolbar_navigation-items {
        display: none;
    }
}

@media (min-width: 769px){
    .toolbar_toggle{
        display:none;
    }
}

.toggle_button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 35px;
    width: 40px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle_button:focus {
    outline: none;
}

.button_line {
    width: 35px;
    height: 4px;
    background:rgb(243, 83, 83);
}
.aboutme_main {
    text-align: center;
}

.aboutme_main_img {
    width: 400px;
    height: 400px;
    border-radius: 400px;
}
.cp_pro {
    font-size: 35px;
    font-style: italic;
}

.logo_img{
    width: 170px;
    height: 60px;
}

.cp_pro a {
    text-decoration: none;
    color:rgb(118, 118, 248);
}

.cp_pro a:hover {
    color:rgb(231, 75, 75);
}
.container {
    border : 2px solid rgb(192, 126, 126) ;
    border-radius: 8px;
    padding: 8px;
    display: flex;
    align-items: center;
    justify-content: center
  }
  
  img {
    max-width: 100%
  }
  
  .image {
    flex-basis: 40%
  }
  
  .text {
    font-size: 20px;
    padding-left: 20px;
  }

  .text a {
    background-color: rgb(66, 111, 196);
    color: white;
    padding: 0.05em .5em;
    text-decoration: none;
    text-transform: uppercase;
  }

  
.logoset {
    text-align: center;
    font-size: x-large;
}

.logoset img{
    width: 50px;
    height:50px;
}

.logoset a {
    text-decoration: none;
    color:rgb(118, 118, 248);
}

.logoset a:hover {
    color:rgb(231, 75, 75);
}

.side_drawer{
    height: 50%;
    background: #f1e8e8;
    box-shadow: 2px 0px 3px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 50%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side_drawer.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.drawer_logo{
    margin-top: 20px;
    width: 200px
}
.side_drawer ul {
    height: 100%;
    list-style: none;
    font-size: 18px;
    font-family: cursive;
    flex-direction: column;
    justify-content: center;
}
.side_drawer li {
    margin: 0.5rem 0;
}

.side_drawer a{
    color:rgb(207, 86, 86);
    text-decoration: none;
    font-size: 1.2rem;
}

.side_drawer a:hover,
.side_drawer a:active{
    color: black;
    cursor: pointer;
}

@media (min-width: 769px){
    .side_drwawer{
        display:none;
    }
}

.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
